@charset "UTF-8";

/* 清除浮动 */

.clearfix:before,
.clearfix:after {
  content: "";
  display: table;
}

.clearfix:after {
  height: 0;
  display: block;
  clear: both;
  visibility: hidden;
  overflow: hidden;
}

.clearfix {
  *zoom: 1;
  /*IE/7/6*/
}

/*公共类*/

.fl {
  float: left;
}

.fr {
  float: right;
}

.hide {
  display: none !important;
}

.visibilityHide {
  visibility: hidden !important;
}

.wrap {
  width: 7.5rem;
  margin: 0 auto;
}

/*弹性盒子 start*/

.flex-father {
  display: flex;
  display: -webkit-box;
  display: -moz-box;
  display: -o-box;
  display: -ms-box;
}

.flex-father .flex-child1 {
  -webkit-box-flex: 1;
}

/*弹性盒子 end*/

/*居中 start*/

.translateCenter {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

/*居中 end*/

.pt10 {
  padding-top: 0.1rem;
}

.pt23 {
  padding-top: 0.23rem;
}

.ft40 {
  font-size: 0.4rem;
}

.w155 {
  width: 1.55rem;
}

.w400 {
  width: 4rem;
}

.w424 {
  width: 4.24rem;
}

/*公用  start*/

.mt30 {
  margin-top: 0.3rem !important;
}

/*文本相关  start*/

.bgWhite {
  background: white;
}

.color-fb343e {
  color: #fb343e;
}

.color-ff7404 {
  color: #ff7404;
}

.font20 {
  font-size: 0.2rem;
}

.font26 {
  font-size: 0.26rem;
}

.textCenter {
  text-align: center;
}

/*文本相关  end*/

.noBorder {
  border: 0 !important;
}

/*按钮  start*/

.bnt-red {
  height: 0.88rem;
  line-height: 0.88rem;
  color: white;
  font-size: 0.3rem;
  text-align: center;
  background: #fb343e;
  border-radius: 0.5rem;
}

.vertical-line {
  border-right: 0.02rem solid;
  display: inline-block;
  height: 0.18rem;
}

/*按钮  end*/

/*公用  end*/


